body {
  /*background-color: #f9f9f9;*/
  background: #f4f4f4;
  min-height: 100vh;
}

a {
  color: #1157ed;
}

.btn {
  font-size: 14px;
  border-radius: 8px;
}

.cursor-default {
  cursor: default !important;
}

.shadowed {
  box-shadow: 0 9px 62px -13px rgba(0, 0, 0, 0.20) !important;
}

.btn-primary {
  background: #1157ed;
  border-color: #1157ed;
}

.launcher-body {
  display: -webkit-flex;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  background: white;
}

.launcher-body .logo-container img {
  height: 150px;
  width: 150px;
}

.launcher-body .logo-container .logo-text h4 {
  font-weight: 700;
  font-size: 45px;
  margin-bottom: 5px;
}

.launcher-body .logo-container .logo-text p {
  margin-bottom: 0;
  font-size: 23px;
}

.launcher-body .logo-container .logo-text {
  margin-top: 10px;
}

.group-heading {
  font-size: 22px;
  font-weight: 600;
  float: left;
}

.header-row h4 {
  margin-top: 8px;
}

.group-heading h4 {
  margin-bottom: 0;
}

.group-heading .small {
  font-size: 14px;
  font-weight: 400;
  margin-left: 3px;
}

.group-filters {
  float: right;
}

.row-group {
  margin-top: 35px;
}

.row-group .group-heading {
  margin: auto;
}

.load-next-date {
  padding: 10px 20px;
  display: block;
  margin: 25px auto auto;
}

.alert-group {
  margin-top: -35px;
  margin-bottom: 25px;
  text-align: center;
}

.alert-group .alert:not(:last-child) {
  border-bottom: none;
}

.alert-group .alert {
  border-top: none;
  margin-bottom: 0;
}

.alert-warning {
  background: #ffa;
}

.no-trending-data.alert {
  margin-top: 1rem;
}

.loading-indicator {
  margin: 50px 0 0;
  text-align: center;
}

.loading-indicator svg {
  height: 85px;
  width: 85px;
}

.top-nav {
  background: #fff;
  border-bottom: 1px solid #e8e8e8;
  padding: 15px 0;
  margin-bottom: 35px;
  position: relative;
  height: 106px;
}

.top-nav .logo {
  margin-left: -10px;
}

.top-nav .logo img {
  width: 64px;
  height: 64px;
  float: left;
}

.top-nav .logo-text {
  float: left;
  margin-left: 10px;
  margin-top: 10px;
}

.top-nav .logo-text h4 {
  margin: 3px 0 0;
  font-weight: 700;
  color: #2d2d2d;
}


.top-nav .logo-text .top-text {
  position: relative;
}
.top-nav .logo-text .top-arrow {
  display: inline-block;
  width: 0;
  height: 0;

  position: absolute;
  top: -4px;
  left: 1px;

  border-top: 4px solid #eee0;
  border-left: 4px solid #eee0;
  border-right: 4px solid #eee0;
  border-bottom: 4px solid #e46773;
}

.top-nav .logo-text p {
  margin-bottom: 0;
}

.top-nav .nav-icon-links {
  margin: 10px 10px 0 0;
}
.top-nav .nav-icon-links .nav-link-item {
  color: #2d2d2d;
  float: left;
  text-align: center;
  width: 24px;
  height: 24px;
  line-height: 24px;
  margin-left: 8px;
  font-size: 20px;
  border-radius: 3px;
}
.top-nav .nav-icon-links .nav-link-item:hover {
  text-decoration: none;
  box-shadow: 0px 1px 3px #666;
  position: relative;
  top: -1px;
}

.top-nav .nav-icon-links .fa-github {
  background: #24292e;
  color: #fff;
}
.top-nav .nav-icon-links .fa-comments {
  font-size: 18px;
  background: #393e46;
  color: #aaef39;
}
.top-nav .nav-icon-links .fa-chrome {
  font-size: 16px;
  background: #4C8CF5;
  color: #fff;
}
.top-nav .nav-icon-links .fa-twitter {
  background: #01aced;
  color: #fff;
}

.language-filter-wrap {
  position: relative;
  z-index: 1;
}

.language-filter {
  border-radius: 8px;
  padding: 10px 20px;
  cursor: default;
  display: block;
  background: white !important;
  border-color: white !important;
}

.language-select {
  position: absolute;
  top: 100%;
  width: 300px;
  margin-top: 5px;
  overflow: hidden;
  font-size: 12px;
  color: #586069;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #e8e8e8;
  border-radius: 8px;
  box-shadow: 0 3px 12px rgba(27, 31, 35, 0.15);
}

.select-menu-header, .select-menu-divider {
  padding: 12px 10px;
  background: white;
  border-bottom: 1px solid #e8e8e8;
  font-size: 15px;
}

.select-menu-header .select-menu-title, .select-menu-divider {
  font-weight: 600;
  color: #24292e;
}

.select-menu-filters {
  background-color: #f4f4f4;
}

.select-menu-text-filter {
  padding: 10px;
  border-bottom: 1px solid #e8e8e8;
}

.select-menu-text-filter input {
  display: block;
  font-size: 14px;
  width: 100%;
  max-width: 100%;
  border: 1px solid #dfe2e5 !important;
  padding: 5px 10px !important;
  border-radius: 3px;
  box-shadow: none !important;
  outline: none !important;
}

.select-menu-list {
  position: relative;
  max-height: 400px;
  overflow: auto;
}

.select-menu-item {
  text-align: left;
  background-color: #fff;
  border-top: 0;
  border-right: 0;
  border-left: 0;
  display: block;
  padding: 10px 8px 10px 16px;
  overflow: hidden;
  color: inherit;
  cursor: pointer;
  border-bottom: 1px solid #e8e8e8;
}

.select-menu-list .select-menu-item:last-child {
  border-bottom: none;
}

.select-menu-item:hover, .select-menu-item.active-item {
  color: #fff;
  background-color: #1157ed;
  text-decoration: none;
}

.select-menu-item:hover .select-menu-item-text,
.select-menu-item.active-item .select-menu-item-text {
  color: white;
}

.select-menu-item-text {
  display: block;
  text-align: left;
}

.view-type {
  border-radius: 8px;
  padding: 10px;
  background: white;
  cursor: default;
}

.view-type button {
  color: #ccc;
  text-decoration: none;
  margin: 0 0 0 10px;
  padding: 0 5px;
  list-style: none;
  border: none;
  background: none;
  cursor: pointer;
  -webkit-appearance: none;
  box-shadow: none;
  outline: none;
}

.view-type button.active, .view-type button:hover {
  color: #343a40;
}

.view-type button:first-child {
  margin-left: 0;
}

.filters-wrap .filter-item {
  float: left;
}

.filters-wrap .filter-item:not(:first-child) {
  margin-left: 15px;
}

.date-jump-type,
.date-jump-type:hover,
.date-jump-type:active {
  border-radius: 8px;
  padding: 10px 20px;
  background: white !important;
  color: black !important;
  border: 1px solid white !important;
  cursor: default;
}

.date-jump-wrap .dropdown-menu {
  border: 1px solid #e8e8e8;
  box-shadow: 0 3px 12px rgba(27, 31, 35, 0.15);
  font-size: 15px;
  line-height: 30px;
  top: 3px !important;
}

.date-jump-wrap .dropdown-menu button {
  cursor: pointer;
  box-shadow: none;
  outline: none;
}

.group-heading h4:first-letter {
  text-transform: uppercase;
}

.list-container {
  background: white;
  padding: 25px;
  margin: 25px 0 0;
  border-radius: 8px;
  box-shadow: -5px 10px 60px -13px rgba(0, 0, 0, 0.20);
}

.list-item-container {
  padding: 0 0 20px;
  margin-bottom: 20px;
  border-bottom: 1px solid #e8e8e8;
  position: relative;
}

.list-item-container .repo-header h3 {
  margin-bottom: 4px;
  font-size: 20px;
  font-weight: 700;
}

.list-item-container .repo-header h3 .text-normal {
  font-weight: 400;
}

.list-item-container .repo-footer {
  font-size: 13px;
}

.list-item-container .repo-header p {
  margin-bottom: 0;
}

.list-item-container .repo-header, .list-item-container .repo-body {
  margin-bottom: 15px;
}

.list-item-container .repo-body p {
  margin-bottom: 0;
  display: block;
  max-width: 80%;
}

.list-item-container .repo-footer svg {
  position: relative;
  top: -1px;
  margin-right: 6px;
  fill: #6c757d;
  vertical-align: text-top;
}

.list-item-container .repo-meta a {
  color: currentColor;
  text-decoration: none;
  font-weight: 500;
}

.list-container .list-item-container:last-child {
  margin-bottom: 0;
  border-bottom: 0;
  padding-bottom: 0;
}

.list-item-container .repo-footer a {
  color: #6c757d;
  text-decoration: none;
}

.list-item-container .author-link {
  position: absolute;
  top: 15px;
  right: 15px;
}

.list-item-container .author-img {
  height: 92px;
  width: 92px;
  border-radius: 92px;
}

.built-by-member-item:first-of-type {
  margin-left: 4px;
}
.built-by-member-item {
  margin-right: 2px;
}
.built-by-member-avatar img {
  width: 20px;
  height: 20px;
  display: inline-block;
  border-radius: 50%;
  vertical-align: middle;
  line-height: 1;
  margin: 0 0 4px 0;
}
.built-by-member-avatar:hover img {
  box-shadow: 0px 2px 3px #333;
}

.built-by-member-popup {
    opacity: 1!important;
    background-color: rgba(0, 0, 0, 0.9)!important;
    padding: 10px 10px 0 10px;
    text-align: center;
}
.built-by-member-popup img {
  width: 100px;
  height: 100px;
  border-radius: 8px;
  display: block;
  margin: auto;
}
.built-by-member-popup .username {
  display: inline-block;
  font-weight: bold;
  height: 30px;
  line-height: 30px;
  font-size: 14px;
}

.grid-item-container {
  padding: 10px;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.grid-item-body {
  background: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: -5px 10px 60px -13px rgba(0, 0, 0, 0.20);
  height: 270px;
}

.grid-item-container .author-details {
  margin-bottom: 20px;
}

.grid-item-container .author-img img {
  width: 35px;
  height: 35px;
  object-fit: cover;
  border-radius: 5px;
  float: left;
  margin-right: 15px;
}

.grid-item-container .author-details h5 {
  margin-bottom: 2px;
  font-weight: 600;
  color: #6c7583;
  font-size: 14px;
}

.grid-item-container .author-details a {
  color: #6c7583;
}

.grid-item-container .author-details p {
  margin-bottom: 0;
  font-size: 12px;
}

.grid-item-container .author-header, .repo-header, .repo-body {
  margin-bottom: 15px;
}

.grid-item-container .author-header a {
  text-decoration: none;
}

.grid-item-container .repo-header h5 {
  font-size: 19px;
  margin-bottom: 5px;
}

.grid-item-container .repo-header p {
  margin-bottom: 0;
}

.grid-item-container .repo-header a {
  text-decoration: none;
}

.grid-item-container .repo-header .repo-meta a {
  color: currentColor;
  font-weight: 500;
}

.repo-language-color {
  border-radius: 50%;
  display: inline-block;
  height: 12px;
  position: relative;
  top: 1px;
  width: 12px;
}

.grid-item-container .author-name {
  font-weight: 400;
}

.grid-item-container .repo-name {
  font-weight: 700;
  word-wrap: break-all;
}

.grid-item-container .repo-body p {
  font-size: 14px;
  margin-bottom: 0;
  color: #6e7583;
  max-height: 100px;
  overflow: hidden;
}

.grid-item-container .repo-footer {
  font-size: 12px;
  position: absolute;
  bottom: 20px;
  left: 20px;
  right: 25px;
  background: white;
  padding: 10px;
}

.grid-item-container .repo-footer a {
  text-decoration: none;
  color: #6c757d;
  font-weight: 500;
}

.grid-item-container .repo-footer svg {
  position: relative;
  top: -2px;
  fill: #6c757d;
  margin-right: 5px;
}

.grid-container {
  margin-top: 15px;
  padding: 0 5px;
}

.options-container {
  margin: 200px auto 20px;
  max-width: 700px;
}

.options-container .container {
  background: white;
  padding: 25px 30px;
  border-radius: 20px;
}

.options-form ul {
  padding: 0;
  margin: 15px 0 15px 25px;
}

.options-form ul li {
  line-height: 30px;
}

.options-form ul li span,
.options-form ul li a {
  background: #ffa;
  padding: 5px 7px;
  color: #343a40;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 500;
}

.options-form ul li a {
  color: blue;
  text-decoration: underline;
}

.options-form .form-field p {
  margin-bottom: 15px;
}

.options-form .form-field input[type="text"] {
  display: block;
  box-shadow: none;
  height: 60px;
  -webkit-box-shadow: none;
  border: 2px solid #ececec;
  margin-bottom: 15px;
}

.options-form .btn-save {
  margin-bottom: 10px;
}

.options-form .btn {
  font-size: 15px;
  padding: 15px 25px;
  height: 60px;
  line-height: 30px;
  display: block;
  width: 100%;
}

